<template><section><h1>GeoJsonDataSource</h1>
<p>The <code v-pre>vc-datasource-geojson</code> component is used to load a dataSource which processes both <a href="https://geojson.org/">GeoJSON</a> and <a href="https://github.com/topojson/topojson">TopoJSON</a> data. simplestyle-spec properties will also be used if they are present.</p>
<h2>Example</h2>
<h3>Load a GeoJsonDataSource with <code v-pre>vc-datasource-geojson</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-datasource-geojson data="./statics/SampleData/lineData/streamline.json" @ready="subReady" :show="show" :options="options" ref="ds" @click="clicked" :entities="entities"></vc-datasource-geojson>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>Code</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-datasource-geojson</span>
        <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
        <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>
        <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"ds"</span>
        <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>
        <span class="hljs-attr">:entities</span>=<span class="hljs-string">"entities"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-datasource-geojson</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">options</span>: {
          <span class="hljs-attr">stroke</span>: <span class="hljs-string">'red'</span>
        },
        <span class="hljs-attr">entities</span>: []
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-number">100</span>; i++) {
          <span class="hljs-keyword">this</span>.entities.push({
            <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">40</span> + <span class="hljs-number">85</span>, <span class="hljs-attr">lat</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">30</span> + <span class="hljs-number">21</span> },
            <span class="hljs-attr">label</span>: {
              <span class="hljs-attr">text</span>: i.toString(),
              <span class="hljs-attr">pixelOffset</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">25</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">20</span> }
            },
            <span class="hljs-attr">point</span>: {
              <span class="hljs-attr">pixelSize</span>: <span class="hljs-number">8</span>,
              <span class="hljs-attr">outlineWidth</span>: <span class="hljs-number">2</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>,
              <span class="hljs-attr">outlineColor</span>: <span class="hljs-string">'yellow'</span>
            }
          })
        }
      },
      subReady(cesiumInstance) {
        cesiumInstance.viewer.zoomTo(cesiumInstance.cesiumObject)
      },
      clicked(e) {
        <span class="hljs-built_in">console</span>.log(e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>Instance Properties</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>required</code> A url, GeoJSON object, or TopoJSON object to be loaded.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> Gets whether or not this data source should be displayed.</td>
</tr>
<tr>
<td>entities</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code> Specify the collection of entities to be added to this datasource.</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> An object with the following properties:</td>
</tr>
<tr>
<td>-------------</td>
<td>---------------------</td>
<td>-------</td>
<td>--------------------------------------------------------------------</td>
</tr>
<tr>
<td>sourceUri</td>
<td>String</td>
<td></td>
<td>The default size of the map pin created for each point, in pixels.</td>
</tr>
<tr>
<td>markerSize</td>
<td>Number</td>
<td></td>
<td></td>
</tr>
<tr>
<td>markerSymbol</td>
<td>String</td>
<td></td>
<td></td>
</tr>
<tr>
<td>markerColor</td>
<td>String|Object|Array</td>
<td></td>
<td></td>
</tr>
<tr>
<td>stroke</td>
<td>String|Object|Array</td>
<td></td>
<td></td>
</tr>
<tr>
<td>strokeWidth</td>
<td>Number</td>
<td></td>
<td></td>
</tr>
<tr>
<td>fill</td>
<td>String|Object|Array</td>
<td></td>
<td></td>
</tr>
<tr>
<td>clampToGround</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td></td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GeoJsonDataSource.html">GeoJsonDataSource</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>Triggers when the underlying data changes.</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>Triggers if an error is encountered during processing.</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>Triggers the data source either starts or stops loading.</td>
</tr>
<tr>
<td>clusterEvent</td>
<td>(clusteredEntities, cluster)</td>
<td>Gets the event that is fired when entities are added or removed from the collection.</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>(collection, added, removed, changed)</td>
<td>Gets the event that will be raised when a new cluster will be displayed.</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the datasource.</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Touch when the mouse clicks outside the datasource.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the datasource.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves on this datasource.</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves to this datasource.</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves out of the datasource.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          show: true,
          options: {
            stroke: 'red'
          },
          entities: []
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
           for (let i = 0; i < 100; i++) {
            this.entities.push({
              position: { lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 },
              label: {
                text: i.toString(),
                pixelOffset: {x: 25, y: 20}
              },
              point: {
                pixelSize: 8,
                outlineWidth: 2,
                color: 'red',
                outlineColor: 'yellow'
              }
            })
          }
        },
        subReady(cesiumInstance) {
          cesiumInstance.viewer.zoomTo(cesiumInstance.cesiumObject)
        },
        clicked(e) {
          console.log(e)
        }
      }
    }
  </script>